.analysTabs{
  border-bottom: none;
  display: flex;
  font-size: 15rem;
  list-style-type: none;
  gap: 30rem;

  :nth-child(2){
    flex-grow: 0;
  }
  .react-tabs__tab--selected{
    background-color: #E1ECF5;
    border-radius: 40rem;
    border: none;
  }
  .myTab{
    div{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40rem;
      height: 40rem;
      border: 1rem solid #464e5f;
      border-radius: 40rem;
      cursor: pointer;
    }

  }
}
